.Search.active input {
    border-radius: 2rem 2rem 0 0;
}

.Search .list {
    position: absolute;
    top: 100%; /* Posiciona logo abaixo do input */
    left: 0;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    max-height: 15rem;
    overflow-y: auto;
    transition: opacity 0.1s ease;
}

.Search.active .list {
    opacity: 1;
    pointer-events: auto;
}

.list li {
    list-style: none;
    padding: 0.5rem 0.9rem;
    width: 100%;
    cursor: pointer;
    border-radius: 3px;
}

.list li:hover {
    background: #efefef;
}
