
.BarraNav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 3.5rem;
}

.BarraNav a{
  color: #fff;
  text-decoration: none;
  font-size: 1.2rem;
}


.cabecario {
  position: relative; /* Mudei para relative */
  width: 100%;
  max-width: 100%;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cabecario .Search {
  position: relative; /* Mudei para relative */
}


.cabecario .icon {
  color: black;
  height: 3rem;
  width: 2rem;
  text-align: center;
  cursor: pointer;
  position: absolute;
  right: 1rem; /* Ajuste conforme necessário */
  top: 50%;
  transform: translateY(-50%);
}

.cabecario input {
  padding: 0.5rem;
  padding-right: 3rem; /* Ajuste para acomodar o ícone */
  background-color: #DCDCDC;
  border-radius: 24rem;
  width: 30rem; /* Ajuste conforme necessário */
}
